<template>
  <div style="background:white;">
    <div class="signdetail">
      <el-table :data="SignDetails" height="524" :highlight-current-row="true" class="tb-edit" border style="width: 100%">
        <el-table-column prop="studentName" label="姓名" width="120">
          <template slot-scope="scope">
            <el-input readonly size="small" v-model="scope.row.studentName" placeholder="请输入内容"></el-input>
            <span>{{scope.row.studentName}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="number" label="学号" width="180">
          <template slot-scope="scope">
            <el-input readonly size="small" v-model="scope.row.number" placeholder="请输入内容"></el-input>
            <span>{{scope.row.number}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="courseName" label="课程" width="180">
          <template slot-scope="scope">
            <el-input readonly size="small" v-model="scope.row.courseName" placeholder="请输入内容"></el-input>
            <span>{{scope.row.courseName}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="checkType" label="签到类型" width="180">
          <template slot-scope="scope">
            <el-input size="small" v-model="scope.row.checkType" placeholder="请输入内容"></el-input>
            <span>{{scope.row.checkType}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="checkTime" label="签到时间" width="180">
          <template slot-scope="scope">
            <el-input size="small" v-model="scope.row.checkTime" placeholder="请输入内容"></el-input>
            <span>{{scope.row.checkTime}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="signState" label="考勤状态" width="150">
          <template slot-scope="scope">
            <el-input size="small" v-model="scope.row.signState" placeholder="请输入内容"></el-input>
            <span>{{scope.row.signState}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="teahcerName" label="授课教师" width="150">
          <template slot-scope="scope">
            <el-input size="small" v-model="scope.row.teahcerName" placeholder="请输入内容"></el-input>
            <span>{{scope.row.teahcerName}}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" fixed="right">
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="primary" size="mini">更新</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    
    <div class="pagination" style="margin-left:40%;margin-top:10px;">
      <el-pagination
        background
        layout="prev, pager, next"
        :current-page.sync="pagination.currentPage"
        :page-size.sync="pagination.currentSize"
        :total="pagination.count"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </div>
    <div class="opearte">
      <el-button type="primary" icon="el-icon-back" @click="back">返回上一级</el-button>
      <el-button type="primary" @click="view">
        签到数据可视化
        <i class="el-icon-view el-icon--right"></i>
      </el-button>
      <el-button type="primary" @click="download">
        下载签到表
        <i class="el-icon-download el-icon--right"></i>
      </el-button>
    </div>
  </div>
</template>

<script>
var FileSaver = require("file-saver");
import { saveAs } from "file-saver";

export default {
  name: "SignDetail",
  created() {
    this.getSignDetails();
    this.findByPage(this.signDate,this.className,this.calendar_id,this.pagination.currentPage,this.pagination.currentSize);
  },
  watch: {
    //监测路由变化，只要变化了就调取路由参数方法将数据存储在本组件
    $route: "getSignDetails"
  },
  data() {
    return {
      SignDetails: [],
      //初始分页条件
      pagination: {
        currentPage: 1, //当前页
        currentSize: 9, //每页显示的记录数
        count: 0 //记录总数
      },
      readonly: true,
      ExcelData: [],
      excelName: "",
      className: "",
      signDate:"",
      calendar_id:"",
      courseName:"",
      checkTime:""
    };
  },
  methods: {
    
    handleClick(row) {
      var token = localStorage.getItem("token");
    },
    getSignDetails() {
      //取由路由带过来的参数
      var data = this.$route.query.SignDetails;
      this.className = this.$route.query.className;
      this.signDate = this.$route.query.signDate;
      this.calendar_id = this.$route.query.calendar_id;
      //将数据放在当前的组件内
      this.SignDetails = data;
      this.ExcelData = this.SignDetails;
    },
    //返回上一级
    back() {
      this.$router.go(-1);
    },
    
    findByPage(signDate,className,calendar_id,currentPage, pageSize) {
      //const params = this.pagination
      //var token = localStorage.getItem("token");
      var token = localStorage.getItem("token");
      if (token) {
        this.$http
          .get(
            "/findByPageDetail?signDate="+signDate+"&className="+className+"&calendar_id="+calendar_id+"&currentPage=" +
              currentPage +
              "&pageSize=" +
              pageSize
          )
          .then(res => {
            //alert(res.data.count)
            this.pagination.count = res.data.count;
            this.SignDetails = res.data.row;
            this.courseName = this.SignDetails[0].courseName;
            //this.checkTime = this.SignDetails[0].checkTime;
            for (var i=0;i<this.SignDetails.length;i++) {
              if (this.SignDetails[i].checkTime != null || this.SignDetails[i].checkTime == "") {
                this.checkTime = this.SignDetails[i].checkTime;
              }
            }
          })
          .catch(err => {
            this.$message({
              type: "error",
              message: "网络异常"
            });
          });
      }
    },
    handleCurrentChange(page) {
      this.findByPage(this.signDate,this.className,this.calendar_id,page, this.pagination.currentSize);
    },
    view() {
      //alert(localStorage.getItem("className"))
      this.$router.push({
        path: "/DataView",
        query: { className: this.className, courseName:this.courseName,checkTime:this.checkTime }
      });
    },
    download() {
      require.ensure([], () => {
        const { export_json_to_excel } = require("@/excel/Export2Excel.js");
        const tHeader = [
          "姓名",
          "学号",
          "课程",
          "签到类型",
          "签到时间",
          "考勤状态",
          "授课教师"
        ]; //tHeader为导出Excel表头名称，
        const filterVal = [
          "studentName",
          "number",
          "courseName",
          "checkType",
          "checkTime",
          "signState",
          "teahcerName"
        ];
        const list = this.ExcelData; // this.SignDetails为服务端返回的签到详情
        const data = this.formatJson(filterVal, list); //filterVal为传进来的数组res的键名
        export_json_to_excel(tHeader, data, this.className+"签到详情表"); //'签到详情表'即为导出Excel名称。
      });
    },
    formatJson(filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]));
    }  
  }
};
</script>
<style>
.opearte {
  margin-top: 30px;
  margin-right: 20px;
  float: right;
}
.tb-edit .el-input {
  display: none;
}
.tb-edit .current-row .el-input {
  display: block;
}
.tb-edit .current-row .el-input + span {
  display: none;
}
</style>


