<template>
  <div style="background-color: #EBEBEB;min-height:900px">
    <div style="width:100%;background-color:#143f6d; overflow: hidden">
      <span class="demonstration" style="float:left;padding-top:10px;color:white;margin-left:50%;">
        <h2 style="color:white">中学生考勤系统</h2>
      </span>
      <span class="demonstration" style="float:right;padding-top:30px;margin-right:1%">
        <span class="message" style="color:white;padding-right:20px;">
          <router-link to="#">
            <a href="#">
              <i class="el-icon-message" style="color:white;"></i>
              <span slot="title" style="color:white;">消息中心</span>
            </a>
          </router-link>
        </span>
        <el-dropdown trigger="click">
          <span class="el-dropdown-link" style="color:white">
            {{username}}，您好!
            <i class="el-icon-caret-bottom el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown" @select="handleClick">
            <el-dropdown-item @click.native="editInfo">个人信息</el-dropdown-item>
            <el-dropdown-item @click.native="updatePass">更新密码</el-dropdown-item>
            <el-dropdown-item @click.native="loginout">退出登录</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </span>
    </div>

    <div>
      <el-row :span="24">
        <el-col :xs="4" :sm="4" :md="4" :lg="4">
          <div>
            <el-menu
              default-active="1"
              class="el-menu-vertical-demo"
              style="min-height:720px"
              background-color="#0d5477"
              text-color="white"
              active-text-color="#ffd04b"
              @select="handleSelect"
            >
              <el-submenu index="1">
                <template slot="title">
                  <i class="el-icon-menu"></i>
                  <span slot="title">签到信息管理</span>
                </template>
                <el-menu-item-group>
                  <el-menu-item index="1-1">
                    <i class="el-icon-search"></i>
                    <span slot="title">查询签到信息</span>
                  </el-menu-item>
                </el-menu-item-group>
              </el-submenu>
            
              <el-submenu index="3" v-if="show">
                <template slot="title">
                  <i class="el-icon-menu"></i>
                  <span slot="title">账号管理</span>
                </template>
                <el-menu-item-group>
                  <el-menu-item index="3-1">
                    <i class="el-icon-circle-plus-outline"></i>
                    <span slot="title">添加账号</span>
                  </el-menu-item>
                  <el-menu-item index="3-2">
                    <i class="el-icon-setting"></i>
                    <span slot="title">教师管理</span>
                  </el-menu-item>
                  <el-menu-item index="3-3">
                    <i class="el-icon-edit-outline"></i>
                    <span slot="title">权限管理</span>
                  </el-menu-item>
                </el-menu-item-group>
              </el-submenu>
              <el-submenu index="4">
                <template slot="title">
                  <i class="el-icon-menu"></i>
                  <span slot="title">班级管理</span>
                </template>
                <el-menu-item-group>
                  <el-menu-item index="4-1" v-if="show">
                    <i class="el-icon-circle-plus-outline"></i>
                    <span slot="title">添加班级</span>
                  </el-menu-item>
                  <el-menu-item index="4-2">
                    <i class="el-icon-search"></i>
                    <span slot="title">班级查询</span>
                  </el-menu-item>
                  <el-menu-item index="4-3">
                    <i class="el-icon-view"></i>
                    <span slot="title">年级统计</span>
                  </el-menu-item>
                </el-menu-item-group>
              </el-submenu>
             <el-submenu index="5">
                <template slot="title">
                  <i class="el-icon-menu"></i>
                  <span slot="title">课程管理</span>
                </template>
                <el-menu-item-group>
                  <el-menu-item index="5-1" v-if="show">
                    <i class="el-icon-upload"></i>
                    <span slot="title">上传课程</span>
                  </el-menu-item>
                  <el-menu-item index="5-2">
                    <i class="el-icon-date"></i>
                    <span slot="title">课程安排</span>
                  </el-menu-item>
                </el-menu-item-group>
              </el-submenu>
              <el-submenu index="6">
                <template slot="title">
                  <i class="el-icon-menu"></i>
                  <span slot="title">学生管理</span>
                </template>
                <el-menu-item-group>
                  <el-menu-item index="6-1" v-if="show">
                    <i class="el-icon-circle-plus-outline"></i>
                    <span slot="title">添加学生</span>
                  </el-menu-item>
                  <el-menu-item index="6-2">
                    <i class="el-icon-search"></i>
                    <span slot="title">学生查询</span>
                  </el-menu-item>
                  <el-menu-item index="6-3" v-if="show">
                    <i class="el-icon-upload" ></i>
                    <span slot="title">上传学生信息</span>
                  </el-menu-item>
                </el-menu-item-group>
              </el-submenu>
            </el-menu>
          </div>
        </el-col>
        <el-col :xs="20" :sm="20" :md="20" :lg="20">
          <!-- <div>
            <div
              style="border: 1px solid #A6A6A6; border-radius:6px; padding:5px; margin:2px; background-color: white"
            >
              <el-breadcrumb separator="/"></el-breadcrumb>
            </div>
          </div>-->

          <div style="margin-top:5px">
            <router-view></router-view>
          </div>
        </el-col>
      </el-row>
    </div>
    <div style="width:100%;background-color:#143f6d; overflow: hidden;height:100px;">
      <span class="demonstration" style="float:left;padding-top:15px;color:white;margin-left:50%;">
        <div class="copyright" style="color:white;margin-top:22px;">Copyright&copy;中国地质大学(北京)</div>
      </span>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
export default {
  name: "Home",
  data() {
    return {
      searchCriteria: "",
      breadcrumbItems: ["查询签到信息"],
      username: "",
      show:false
    };
  },
  created() {
    var token = localStorage.getItem("token");
    if (token) {
      this.$router.push("/Courses");
      var number = localStorage.getItem("number");
      this.getUserName(number);
    } else {
      this.$router.push("/Login");
    }
    var role = localStorage.getItem("role");
    alert(role)
    if (role == "2") {
      this.show = true;
    }else {
      this.show = false;
    }
  },
  mounted() {},
  methods: {
    getUserName(number) {
      this.$http.get("/getTeacherName?number="+number).then((res)=>{
          if (res.data.code == 200) {
            this.username = res.data.username;
            localStorage.setItem("username",this.username);
             //alert(res.data.role)
            localStorage.setItem("role",res.data.role);
          }
      }).catch((err)=>{

      })
    },
    loginout() {
      this.$confirm("确定注销吗?")
        .then(_ => {
          var token = localStorage.getItem("token");
          if (token) {
            this.$http({
              methods: "get",
              url: "/loginout",
              headers: { token: token }
            })
              .then(response => {
                if (response.data.code == 200) {
                  localStorage.removeItem("token");
                  localStorage.removeItem("username");
                  localStorage.removeItem("role");
                  this.$message({
                    type: "success",
                    message: "注销成功！"
                  });
                  this.$router.push("/Login");
                } else {
                  this.$message({
                    type: "error",
                    message: "网络错误!"
                  });
                }
              })
              .catch(error => {
                this.$message({
                  type: "error",
                  message: "网络错误!"
                });
              });
          } else {
            this.$message({
              type: "error",
              message: "网络错误!"
            });
            this.$router.push("/Login");
          }
        })
        .catch(_ => {});
    },
    updatePass() {
      var token = localStorage.getItem("token");
      if (token) {
        this.$router.push("/ChangePass");
      } else {
        this.$router.push("/Login");
      }
    },
    editInfo() {
      var token = localStorage.getItem("token");
      if (token) {
        //alert("fuck")
        this.$router.push("/EditInfo");
      } else {
        this.$router.push("/Login");
      }
    },
    handleClick(key, keyPath) {
      switch (key) {
        case "1":
          console.log("info");
          break;
        case "2":
          this.loginout();
          break;
      }
    },

    handleSelect(key, keyPath) {
      switch (key) {
        case "1-1":
          this.$router.push("/Courses");
          this.breadcrumbItems = ["查询签到信息"];
          break;
        case "3-1":
          this.$router.push("/AddAccount");
          this.breadcrumbItems = ["添加账号"];
          break;
        case "3-2":
          this.$router.push("/ManagerTeacher");
          this.breadcrumbItems = ["权限管理"];
          break;
        case "3-3":
          //this.$router.push("/UploadFile");
          this.breadcrumbItems = ["权限管理"];
          break;
          
        case "4-1":
          this.$router.push("/AddClass");
          this.breadcrumbItems = ["添加班级"];
          break;
        case "4-2":
          this.$router.push("/ClassManager");
          this.breadcrumbItems = ["查询班级"];  
          break;
        case "4-3":
          this.$router.push("/Analysis");
          this.breadcrumbItems = ["班级统计"];  
          break;
        case "5-1":
          this.$router.push("/UploadCourse");
          this.breadcrumbItems = ["上传课程"];
          break;
        case "5-2":
          this.$router.push("/LessionManager");
          this.breadcrumbItems = ["课程安排"];
          break; 
        case "6-1":
          this.$router.push("/AddStudent");
          this.breadcrumbItems = ["添加学生"];
          break;
        case "6-2":
          this.$router.push("/StudentManager");
          this.breadcrumbItems = ["查询学生"];
          break; 
        case "6-3":
          this.$router.push("/uploadStudentInfo");
          this.breadcrumbItems = ["上传学生信息"];
          break; 
          
          
      }
    }
  }
};
</script>
<style scoped>
.el-row {
  background: white;
}
.el-input--prefix .el-input__inner {
  padding-left: 30px;
}
.message a:link {
  color: white;
}
a:active {
  color: white;
}
a {
  text-decoration: none;
}
</style>





