<template>
  <div class="bg-login" style="margin-top:40px;">
    <h3>添  加  学  生</h3>
    <hr/>
    <div class="loginscreen bg-white container" style="margin-left:125px;">
      <!-- middle-boxin -->
      <el-form
        inline-message
        :model="studentForm"
        :rules="addRules"
        ref="studentForm"
        class="demo-ruleForm"
        style="padding-top:150px;"
        label-width="30%"
      >
        <el-form-item label="学  号:" required>
          <!-- style="padding-left:15%;" -->
          <el-input
            class="input-editinfo"
            style="width:400px;"
            v-model="studentForm.studentId"
            placeholder="请输入学号"
            @change="validateNumber"
          ></el-input>
        </el-form-item>

        <el-form-item label="姓  名:" required>
          <!-- style="float:right;padding-right:15%;" -->
          <el-input class="input-editinfo" style="width:400px;" id="studentId" v-model="studentForm.studentName" placeholder="请输入姓名"  status-icon:true></el-input>
        </el-form-item>

        <el-form-item label="班  级:" required>
          <el-select v-model="studentForm.classNo" placeholder="请选择班级:">
            <el-option label="初一(1)班" value="101"></el-option>
            <el-option label="初一(2)班" value="102"></el-option>
            <el-option label="初一(3)班" value="103"></el-option>
            <el-option label="初一(4)班" value="104"></el-option>
            <el-option label="初一(5)班" value="105"></el-option>
            <el-option label="初二(1)班" value="201"></el-option>
            <el-option label="初二(2)班" value="202"></el-option>
            <el-option label="初二(3)班" value="203"></el-option>
            <el-option label="初二(4)班" value="204"></el-option>
            <el-option label="初二(5)班" value="205"></el-option>
            <el-option label="初三(1)班" value="301"></el-option>
            <el-option label="初三(2)班" value="302"></el-option>
            <el-option label="初三(3)班" value="303"></el-option>
            <el-option label="初三(4)班" value="304"></el-option>
            <el-option label="初三(5)班" value="305"></el-option>
            <el-option label="高一(1)班" value="401"></el-option>
            <el-option label="高一(2)班" value="402"></el-option>
            <el-option label="高一(3)班" value="403"></el-option>
            <el-option label="高一(4)班" value="404"></el-option>
            <el-option label="高一(5)班" value="405"></el-option>
            <el-option label="高二(1)班" value="501"></el-option>
            <el-option label="高二(2)班" value="502"></el-option>
            <el-option label="高二(3)班" value="503"></el-option>
            <el-option label="高二(4)班" value="504"></el-option>
            <el-option label="高二(5)班" value="505"></el-option>
            <el-option label="高三(1)班" value="601"></el-option>
            <el-option label="高三(2)班" value="602"></el-option>
            <el-option label="高三(3)班" value="603"></el-option>
            <el-option label="高三(4)班" value="604"></el-option>
            <el-option label="高三(5)班" value="605"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label>
          <el-button type="primary" style="width:150px;" @click="Save()">添加</el-button>
          <!-- style="margin-left:1100px;" -->
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: "AddStudent",
  inject: ["reload"],
  data() {
    return {
      studentForm: {
        studentId: "",
        studentName: "",
        classNo:""
      },
      addRules: {
        studentId: [
          { required: true, message: "学号不能为空", trigger: "blur" },
          { min: 5, max: 20, message: "长度在5到20个字符", trigger: "blur" }
        ],
        studentName: [
          { required: true, message: "用户名不能为空", trigger: "blur" },
          { min: 2, max: 20, message: "长度在 2 到 20 个字符", trigger: "blur" }
        ],
        
      }
    };
  },
  created() {
    
  },
  mounted() {
  },
  methods: {
    validateNumber: function(value) {
      if (value == "") {
        this.$message({
          type: "error",
          message: "学号不能为空"
        });
      } else {
        if (value != "") {
          var reg = 5 && /^(\d{1,13})$/;
          if (!reg.test(value)) {
            this.$message({
              type: "error",
              message: "请输入有效的学号"
            });
          }
        }
      }
    },
    Save: function() {
      this.$refs.studentForm.validate(valid => {
        if (valid) {
          var token = localStorage.getItem("token");
          //alert("token = " + token);
          //this.readonly = false
          if (token) {
            this.$http.post("/addStudent",this.studentForm,{headers:{"token":token}})
              .then(res => {
                console.log(res.data);
                if (res.data.code === 200) {
                  //console.log("success");
                  this.$notify({
                    type: "success",
                    message: "添加成功!"
                  });
                  this.$router.push({path:"/StudentDetail",query:{classNo:this.studentForm.classNo}})
                }  else {
                  this.$notify({
                    type: "error",
                    message: "添加失败"
                  });
                }
              })
              .catch(err => {
                this.$message({
                  type: "error",
                  message: "网络错误，请重试"
                });
              });
          }
        } else {
         this.$notify({
                  type: "error",
                  message: "身份过期，请重新登录!"
                });
        }
      });
    }
  }
};
</script>

<style>
body {
  background: url("~@/assets/bk1.jpg") no-repeat center fixed;
  background-size: cover;
}
/* .container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
} */
.el-form-item__error--inline {
  display: flex;
}

.demo-ruleForm{
  margin-top: -30px;
}
.demo-ruleForm {
  padding-top: 65px;
}
.bg-white {
  background-color: #ffffff;
  opacity: 0.8;
}
.middle-boxin {
  z-index: 100;
  margin: 0 auto;
  padding-bottom: 10px;
  /* min-width: 600px; */
  height: 400px;
}
.loginscreen .middle-box {
  width: 300px;
}
.login-title {
  height: 85px;
  padding: 30px;
  font-weight: bold;
}
.login-header {
  height: 150px;
}
.login-logo {
  margin-top: 40px;
}
.login-logo-zh,
.login-logo-en {
  font-weight: bold;
  color: #3b3127;
}
.login-logo-zh {
  font-size: 45px;
  font-family: "宋体";
}
.login-logo-en {
  font-size: 21px;
  font-family: "Helvetica";
}
/* .middle-box {
  .el-input {
    display: inline-block;
  }
  .el-button {
    display: inline-block;
    width: 100%;
  }
  .el-input__inner {
    text-align: center;
  }
} */

.show-pwd {
  position: absolute;
  right: 10px;
  top: 2px;
  font-size: 16px;
  color: #889aa4;
  cursor: pointer;
  user-select: none;
}
.input-editinfo {
  width: 500px;
}
.el-row {
  background: white;
}
.register {
  float: left;
}
.forget_password {
  float: right;
}
.el-input--prefix .el-input__inner {
  padding-left: 10px;
}
.el-select {
  width: 400px;
}
.input-editinfo {
  width: 400px;
}
</style>


