<template>
  <div class="details" style="background:white;">
    <div class="operation1">
      <el-form
        :inline="true"
        :model="Teachers"
        :rules="qurryFormRules"
        ref="Teachers"
        class="demo-form-inline"
      >
        <el-form-item label="教师姓名:" required>
          <el-input v-model="Teachers.name" class="name" placeholder="请输入教师姓名"></el-input>
        </el-form-item>
        <el-form-item label="教师工号:" required style="padding-left:200px;">
          <el-input v-model="Teachers.number" class="number" placeholder="请输入教师工号"></el-input>
        </el-form-item>
        <el-form-item style="padding-left:300px;">
          <el-button type="danger" @click="reset">重置</el-button>
          <el-button type="primary" @click="onSubmit">查询</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="table clearfix" style="margin-top:-15px;">
      <el-table
        :data="TeacherInfo"
        :highlight-current-row="true"
        height="524"
        border
        class="tb-edit"
        style="width: 100%;"
      >
        <el-table-column prop="number" label="工号" width="200">
          <template slot-scope="scope">
            <el-input readonly size="small" v-model="scope.row.number" placeholder="请输入内容"></el-input>
            <span>{{scope.row.number}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="name" label="姓名" width="200">
          <template slot-scope="scope">
            <el-input size="small" v-model="scope.row.name" placeholder="请输入内容"></el-input>
            <span>{{scope.row.name}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="sex" label="性别" width="200">
          <template slot-scope="scope">
            <el-input size="small" v-model="scope.row.sex" placeholder="请输入内容"></el-input>
            <span>{{scope.row.sex}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="email" label="邮箱" width="200">
          <template slot-scope="scope">
            <el-input size="small" v-model="scope.row.email" placeholder="请输入内容"></el-input>
            <span>{{scope.row.email}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="role" label="权限" width="200">
          <template slot-scope="scope">
            <el-select class="input-editinfo" v-model="scope.row.role" placeholder="请输入等级" style="width:188px" >
              <el-option label="教务主任" value="2"></el-option>
              <el-option label="班主任" value="1"></el-option>
              <el-option label="任课教师" value="0"></el-option>
            </el-select>
          </template>
        </el-table-column>
        <el-table-column label="操作" fixed="right">
          <template slot-scope="scope">
            <el-button @click="updateClick(scope.row)" type="primary" size="mini">更新</el-button>
            <el-button @click="deleteClick(scope.row)" type="danger" size="mini">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination" style="margin-left:40%;">
      <el-pagination
        background
        layout="prev, pager, next"
        :current-page.sync="pagination.currentPage"
        :page-size.sync="pagination.currentSize"
        :total="pagination.count"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
export default {
  name: "ManagerTeacher",
  data() {
    return {
      Teachers: {
        number: "",
        name: "",
        sex: "",
        email: "",
        role: ""
      },
      readonly: true,
      //初始分页条件
      pagination: {
        currentPage: 1, //当前页
        currentSize: 9, //每页显示的记录数
        count: 0 //记录总数
      },
      Teacherss: [],
      excelName: "",
      TeacherInfo: [],
      qurryFormRules: {}
    };
  },
  mounted() {},
  created() {
    this.getAllTeahers();
    this.findByPage(this.pagination.currentPage, this.pagination.currentSize);
  },
  methods: {
    getAllTeahers() {
      var token = localStorage.getItem("token");
      if (true) {
        this.$http
          .get("/getAllTeacher", { headers: { token: token } })
          .then(res => {
            if (res.data.code == 200) {
              console.log("data = " + res.data.data);
              this.TeacherInfo = res.data.data;
              for (var i = 0; i < this.TeacherInfo.length; i++) {
                if (this.TeacherInfo[i].role == "1") {
                  this.TeacherInfo[i].role = "任课教师";
                }else if(this.TeacherInfo[i].role == "2") {
                    this.TeacherInfo[i].role = "教务主任";
                }
              }
              return this.TeacherInfo;
            }
          })
          .catch(err => {
            this.$message({
              type: "error",
              message: "网络异常"
            });
          });
      }
    },
    updateClick(row) {
      var token = localStorage.getItem("token");
      if (true) {
        this.$confirm("确定更新吗?")
          .then(_ => {
            var teacher = {};
            teacher.number = row.number;
            teacher.name = row.name;
            teacher.email = row.email;
            teacher.sex = row.sex;
            teacher.role = row.row;
            this.$http
              .post("/updateTeacherInfo", teacher)
              .then(res => {
                if (res.data.code == 200) {
                  this.$notify({
                    type: "success",
                    message: "更新成功"
                  });
                  this.$router.push("/ManagerTeacher");
                  this.TeacherInfo = this.getAllTeahers();
                  this.findByPage(this.pagination.currentPage, this.pagination.currentSize);
                } else {
                  this.$notify({
                    type: "error",
                    message: "更新失败"
                  });
                }
              })
              .catch(err => {
                this.$notify({
                  type: "error",
                  message: "网络错误！"
                });
              });
          })
          .catch(_ => {});
      } else {
        this.$notify({
          type: "error",
          message: "身份过期，请重新登录"
        });
        this.$router.push("/Login");
      }
    },
    deleteClick(row) {
      var token = localStorage.getItem("token");
      if (true) {
        this.$confirm("确定删除吗?")
          .then(_ => {
            this.$http
              .get("/deleteTeacherByNumber?number=" + row.number)
              .then(res => {
                if (res.data.code == 200) {
                  this.$notify({
                    type: "success",
                    message: "操作成功"
                  });
                  this.$router.push("/ManagerTeacher");
                  this.TeacherInfo = this.getAllTeahers();
                  this.findByPage(this.pagination.currentPage, this.pagination.currentSize);
                } else {
                  this.$notify({
                    type: "error",
                    message: "操作失败"
                  });
                }
              })
              .catch(err => {
                this.$message({
                  type: "error",
                  message: err.data.message
                });
              });
          })
          .catch(_ => {});
      } else {
        this.$notify({
          type: "error",
          message: "身份过期，请重新登录"
        });
        this.$router.push("/Login");
      }
    },
    //分页
    findByPage(currentPage, pageSize) {
      //const params = this.pagination
      var token = localStorage.getItem("token");
      if (true) {
        this.$http
          .get(
            "/findTeacherByPage?currentPage=" +
              currentPage +
              "&pageSize=" +
              pageSize
          )
          .then(res => {
            this.pagination.count = res.data.count;
            this.TeacherInfo = res.data.row;
            for (var i = 0; i < this.TeacherInfo.length; i++) {
                if (this.TeacherInfo[i].role == "1") {
                  this.TeacherInfo[i].role = "任课教师";
                }else if(this.TeacherInfo[i].role == "2") {
                    this.TeacherInfo[i].role = "教务主任";
                }
              }
          })
          .catch(err => {
            this.$message({
              type: "error",
              message: "网络异常"
            });
          });
      }
    },
    handleCurrentChange(page) {
      this.findByPage(page, this.pagination.currentSize);
    },
    reset() {
      this.Teachers.number = "";
      this.Teachers.name = "";
      this.getAllTeahers();
    },
    onSubmit: function() {
      var token = localStorage.getItem("token");
      //alert(token)
      if (token) {
        this.$http
          .post("/findTeacherByCondition", this.Teachers)
          .then(res => {
            if (res.data.code == 200) {
              this.TeacherInfo = res.data.data;
            } else {
              this.$notify({
                type: "error",
                message: "不存在该教师"
              });
            }
          })
          .catch(err => {
            this.$message({
              type: "error",
              message: err.data.message
            });
          });
      }else{
        this.$notify({
          type:"error",
          message:"身份过期，请重新登录!"
        });
        this.$router.push("/Login")
      }
    }
  }
};
</script>
<style scoped>
.operation1 {
  padding-bottom: 5px;
  padding-top: 5px;
}
.clearfix::after {
  content: "";
  display: block;
  clear: both;
  padding-bottom: 20px;
}

.tableWrap {
  margin-top: 50px;
  border: 1px solid #333;
  height: 500px;
  width: 100%;
}

.tableWrap .item {
  width: 100px;
  border-right: 1px solid #333;
  float: left;
  height: 100%;
}
.tableWrap .item .name {
  height: 60px;
  line-height: 60px;
  text-align: center;
  border-bottom: 1px solid #333;
}
.tableWrap .item .itemdetail {
  height: 40px;
  line-height: 40px;
  border-bottom: 1px solid #333;
}
.el-form--inline .el-form-item {
  margin-right: 0px;
}
.el-row {
  background-color: white;
}
.el-input__inner {
  padding-left: 30px;
}

.tb-edit .el-input {
  display: none;
}
.tb-edit .current-row .el-input {
  display: block;
}
.tb-edit .current-row .el-input + span {
  display: none;
}

.el-select-dropdown {
  min-width: 200px;
}
</style>


