<template>
  <div class="bg-login" style="margin-top:30px;">
    <h3>添  加  班  级</h3>
      <hr/>
    <div class="loginscreen bg-white container" style="margin-left:125px;">
      <!-- middle-boxin -->
      
      <el-form
        inline-message
        :model="classForm"
        :rules="registerRules"
        ref="classForm"
        class="demo-ruleForm"
        style="padding-top:150px;"
        label-width="30%"
      >
        <el-form-item label="班级名:" required>
          <!-- style="padding-left:15%;" -->
          <el-input
            class="input-editinfo"
            style="width:400px;"
            v-model="classForm.grade"
            placeholder="请输入班级名:"
          ></el-input>
        </el-form-item>
        <el-form-item label="班级号:" required>
          <!-- style="padding-left:15%;" -->
          <el-input
            class="input-editinfo"
            v-model="classForm.classNo"
            style="width:400px;"
            placeholder="请输入班级号:"
            @change="validateclassNo"
          ></el-input>
        </el-form-item>
        <el-form-item label>
          <el-button type="primary" style="width:150px;" @click="Save()">添加</el-button>
          <!-- style="margin-left:1100px;" -->
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: "AddClass",
  inject: ["reload"],
  data() {
    return {
      classForm: {
        classNo: "",
        grade: ""
      },
      readonly:true,
      ackpassword: "",
      registerRules: {
        classNo: [
          { required: true, message: "班级号不能为空", trigger: "blur" },
          { min: 3, max: 20, message: "长度在3到20个字符", trigger: "blur" }
        ],
        grade: [
          { required: true, message: "班级名不能为空", trigger: "blur" },
          { min: 3, max: 20, message: "长度在3到 20 个字符", trigger: "blur" }
        ]
      }
    };
  },
  created() {
  },
  mounted() {
    
  },
  watch:{
     
  },
  methods: {
    validateclassNo: function(value) {
      if (value == "") {
        this.$message({
          type: "error",
          message: "班级号不能为空"
        });
      } else {
        if (value != "") {
          var reg = 3 && /^(\d{1,13})$/;
          if (!reg.test(value)) {
            this.$message({
              type: "error",
              message: "请输入有效的班级号"
            });
          }
        }
      }
    },
    Save: function() {
      this.$refs.classForm.validate(valid => {
        if (valid) {
          var token = localStorage.getItem("token");
          //alert("token = " + token);
          this.readonly = false
          if (true) {
            this.$http.post("/insertClass",this.classForm,{headers:{"token":token}})
              .then(res => {
                console.log(res.data);
                if (res.data.code === 200) {
                  //console.log("success");
                  this.$notify({
                    type: "success",
                    message: "添加成功"
                  });
                  this.$router.push("/ClassManager");
                } else {
                  this.$notify({
                    type: "error",
                    message: "添加失败"
                  });
                } 
              })
              .catch(err => {
                this.$message({
                  type: "error",
                  message: "网络错误，请重试"
                });
              });
          }
        } else {
         this.$notify({
                  type: "error",
                  message: "身份过期，请重新登录"
                });
              this.$router.push("/Login")
        }  
      });
    }
  }
};
</script>

<style>
body {
  background: url("~@/assets/bk1.jpg") no-repeat center fixed;
  background-size: cover;
}
/* .container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
} */
.el-form-item__error--inline {
  display: flex;
}

.demo-ruleForm{
  margin-top: -30px;
}
.demo-ruleForm {
  padding-top: 65px;
}
.bg-white {
  background-color: #ffffff;
  opacity: 0.8;
}
.middle-boxin {
  z-index: 100;
  margin: 0 auto;
  padding-bottom: 10px;
  /* min-width: 600px; */
  height: 400px;
}
.loginscreen .middle-box {
  width: 300px;
}
.login-title {
  height: 85px;
  padding: 30px;
  font-weight: bold;
}
.login-header {
  height: 150px;
}
.login-logo {
  margin-top: 40px;
}
.login-logo-zh,
.login-logo-en {
  font-weight: bold;
  color: #3b3127;
}
.login-logo-zh {
  font-size: 45px;
  font-family: "宋体";
}
.login-logo-en {
  font-size: 21px;
  font-family: "Helvetica";
}
/* .middle-box {
  .el-input {
    display: inline-block;
  }
  .el-button {
    display: inline-block;
    width: 100%;
  }
  .el-input__inner {
    text-align: center;
  }
} */


.input-editinfo {
  width: 400px;
}
.el-row {
  background: white;
}
.register {
  float: left;
}
.forget_password {
  float: right;
}
.el-input--prefix .el-input__inner {
  padding-left: 10px;
}
a {
  text-decoration: none;
}

.input-editinfo {
  width: 400px;
}
</style>


