<template>
  <div class="bg-login" style="margin-top:30px;">
    <h3>添  加  教  师  账  户</h3>
    <hr/>
    <div class="loginscreen bg-white container">
      <!-- middle-boxin -->
      <el-form
        inline-message
        :model="account"
        :rules="registerRules"
        ref="account"
        class="demo-ruleForm"
        style="padding-top:130px;"
        label-width="30%"
      >
        <el-form-item label="工  号:" required>
          <!-- style="padding-left:15%;" -->
          <el-input
            class="input-editinfo"
            v-model="account.number"
            placeholder="请输入工号"
            @change="validateNumber"
          ></el-input>
        </el-form-item>

        <el-form-item label="姓  名:" required>
          <!-- style="float:right;padding-right:15%;" -->
          <el-input class="input-editinfo" v-model="account.name" placeholder="请输入姓名"  status-icon:true></el-input>
        </el-form-item>

        <el-form-item label="性  别:" required >
          <!-- style="padding-left:15%;" -->
          <el-select class="input-editinfo" v-model="account.sex" placeholder="请输入性别" >
            <el-option label="男" value="男"></el-option>
            <el-option label="女" value="女"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="邮 箱:" required>
          <!-- style="float:right;padding-right:15%;" -->
          <el-input
            class="input-editinfo"
            v-model="account.email"
            placeholder="请输入邮箱"
            @change="validateEmail(account.email)"
          ></el-input>
        </el-form-item>
        <el-form-item label="等 级:" required >
          <!-- style="padding-left:15%;" -->
          <el-select class="input-editinfo" v-model="account.role" placeholder="请输入等级" >
            <el-option label="教务主任" value="2"></el-option>
            <el-option label="班主任" value="1"></el-option>
            <el-option label="任课教师" value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label>
          <el-button type="primary" style="width:150px;" @click="Save()">添加</el-button>
          <!-- style="margin-left:1100px;" -->
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: "AddAccount",
  inject: ["reload"],
  data() {
    return {
      account: {
        number: "",
        name: "",
        password: "",
        sex: "",
        email: "",
        role:""
      },
      readonly:true,
      registerRules: {
        number: [
          { required: true, message: "工号不能为空", trigger: "blur" },
          { min: 4, max: 20, message: "长度在4到20个字符", trigger: "blur" }
        ],
        name: [
          { required: true, message: "用户名不能为空", trigger: "blur" },
          { min: 3, max: 20, message: "长度在 3 到 20 个字符", trigger: "blur" }
        ],
        sex: [{ required: true, message: "性别不能为空", trigger: "blur" }],
        email: [
          { required: true, message: "邮箱不能为空", trigger: "blur" },
          {
            min: 10,
            max: 20,
            message: "邮箱长度在10到20个字符",
            trigger: "blur"
          }
        ]
      }
    };
  },
  created() {
    
  },
  mounted() {
  },
  methods: {
    validateNumber: function(value) {
      if (value == "") {
        this.$message({
          type: "error",
          message: "工号不能为空"
        });
      } else {
        if (value != "") {
          var reg = 4 && /^(\d{1,13})$/;
          if (!reg.test(value)) {
            this.$message({
              type: "error",
              message: "请输入有效的工号"
            });
          }
        }
      }
    },
    validateEmail: function(value) {
      if (value === "") {
        this.$message({
          type: "error",
          message: "请正确填写邮箱"
        });
      } else {
        if (value !== "") {
          var reg = /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
          if (!reg.test(value)) {
            this.$message({
              type: "error",
              message: "请输入有效的邮箱"
            });
          }
        }
      }
    },
    Save: function() {
      this.$refs.account.validate(valid => {
        if (valid) {
          var token = localStorage.getItem("token");
          //alert("token = " + token);
          this.readonly = false
          if (token) {
            this.$http.post("/addAccount",this.account,{headers:{"token":token}})
              .then(res => {
                console.log(res.data);
                if (res.data.code === 200) {
                  //console.log("success");
                  this.$notify({
                    type: "success",
                    message: "添加账户成功"
                  });
                  this.$router.push("/ManagerTeacher");
                }else if(res.data.code == 1001){
                    this.$notify({
                    type: "error",
                    message: res.data.message
                  });
                }else {
                  this.$notify({
                    type: "error",
                    message: "添加失败"
                  });
                  return;
                }
              })
              .catch(err => {
                this.$notify({
                  type: "error",
                  message: "网络错误，请重试"
                });
              });
          }else {
              this.$notify({
                    type: "error",
                    message: "身份过期，请重新登录"
                  });
                  this.$router.push("/Login")
          }
        }
      });
    }
  }
};
</script>

<style>
body {
  background: url("~@/assets/bk1.jpg") no-repeat center fixed;
  background-size: cover;
}
/* .container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
} */
.el-form-item__error--inline {
  display: flex;
}

.demo-ruleForm{
  margin-top: -30px;
}
.demo-ruleForm {
  padding-top: 65px;
}
.bg-white {
  background-color: #ffffff;
  opacity: 0.8;
}
.middle-boxin {
  z-index: 100;
  margin: 0 auto;
  padding-bottom: 10px;
  /* min-width: 600px; */
  height: 400px;
}
.loginscreen .middle-box {
  width: 300px;
}
.login-title {
  height: 85px;
  padding: 30px;
  font-weight: bold;
}
.login-header {
  height: 150px;
}
.login-logo {
  margin-top: 40px;
}
.login-logo-zh,
.login-logo-en {
  font-weight: bold;
  color: #3b3127;
}
.login-logo-zh {
  font-size: 45px;
  font-family: "宋体";
}
.login-logo-en {
  font-size: 21px;
  font-family: "Helvetica";
}
/* .middle-box {
  .el-input {
    display: inline-block;
  }
  .el-button {
    display: inline-block;
    width: 100%;
  }
  .el-input__inner {
    text-align: center;
  }
} */

.show-pwd {
  position: absolute;
  right: 10px;
  top: 2px;
  font-size: 16px;
  color: #889aa4;
  cursor: pointer;
  user-select: none;
}
.input-editinfo {
  width: 500px;
}
.el-row {
  background: white;
}
.register {
  float: left;
}
.forget_password {
  float: right;
}
.el-input--prefix .el-input__inner {
  padding-left: 10px;
}
a {
  text-decoration: none;
}
a:link {
  color: blue;
}
a:visited {
  color: #409eff;
}
a:hover {
  color: red;
  font-size: 17px;
}
a:active {
  color: lightskyblue;
}
</style>


