<template>
  <div class="uploadfile">
    <div>
      <div class="upload" style="float:left;">
        <el-upload
          action
          :on-change="uploadFile"
          :auto-upload="false"
          :show-file-list="false"
          accept=".xls, .xlsx"
          :on-remove="handleRemove"
        >
          <el-button size="small" type="primary">选择文件</el-button>
        </el-upload>
      </div>
      <div class="operation">
        <el-button size="small" type="primary" style="margin-left:30px; float:left;" @click="upload">上传</el-button>
      </div>
      <div class="operation">
        <el-button size="small" type="primary" style="margin-left:30px;" @click="download">下载模板</el-button>
      </div>
    </div>
    <div slot="tip" class="el-upload__tip">只 能 上 传 xlsx / xls 文 件</div>

    <hr />
    <div>
      <div class="students">
        <el-table :data="StudentInfo" height="640" border style="width: 100%">
          <el-table-column prop="classNo" label="班级号" width="310"></el-table-column>
          <el-table-column prop="className" label="班级名" width="310"></el-table-column>
          <el-table-column prop="studentId" label="学号" width="310"></el-table-column>
          <el-table-column prop="studentName" label="姓名" width="310"></el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "UploadStudentInfo",
  data() {
    return {
      fileTemp: null,
      StudentInfo: [],
      excelData:[{"studentId":'2104170010',"studentName":"张三","className":"初一(1)班","teacherName":"李四"}]
    };
  },
  methods: {
    download(){
      require.ensure([], () => {
        const { export_json_to_excel } = require("@/excel/Export2Excel.js");
        const tHeader = ["学号", "姓名", "班级", "班主任"]; //tHeader为导出Excel表头名称，
        const filterVal = [
          "studentId",
          "studentName",
          "className",
          "teacherName"
        ];
        const list = this.excelData; // this.SignDetails为服务端返回的签到详情
        const data = this.formatJson(filterVal, list); //filterVal为传进来的数组res的键名
        export_json_to_excel(tHeader, data, "学生信息模板"); //'签到详情表'即为导出Excel名称。
      });
    },
    formatJson(filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]));
    },
    handleRemove(file, fileList) {
      this.fileTemp = null;
    },
    upload() {
      console.log("开始上传文件.....");
      this.$http.post("/uploadStudents",this.StudentInfo).then((res)=>{
          if (res.data.code == 200) {
            this.$notify({
              type:'success',
              message:'上传成功！'
            });
            this.$router("/StudentManager");
          }else if(res.data.code == 1001){
              this.$notify({
              type:'error',
              message:'上传失败，班级学生已存在！'
            });
          }else {
            this.$message({
              type:'error',
              message:'上传失败！'
            });
          }
      }).catch((err)=>{
          this.$message({
              type:'error',
              message:err.data.message
            });
      })
    },
    //读取excel文件
    uploadFile(file, fileList) {
      this.fileTemp = file.raw;
      console.log(this.fileTemp);
      if (this.fileTemp) {
        if (
          this.fileTemp.type ==
            "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet" ||
          this.fileTemp.type == "application/vnd.ms-excel"
        ) {
          //也可以用if(!/\.(xls|xlsx)$/.test(this.fileTemp.name.toLowerCase()))来判断
          this.importFile(this.fileTemp, outdata => {
            //上传成功后回调
            let arr = [];
            outdata.map(v => {
              let obj = {};
              obj.classNo = v["班级号"];
              obj.className = v["班级名"]
              obj.studentId = v["学号"];
              obj.studentName = v["姓名"];
              arr.push(obj);
            });
            this.StudentInfo = arr;
          });
        } else {
          this.$message({
            type: "warning",
            message: "附件格式错误，请删除后重新上传！"
          });
        }
      } else {
        this.$message({
          type: "warning",
          message: "请上传附件！"
        });
      }
    },
    //读取数据
    importFile(obj, callback) {
      let _this = this;
      // 通过DOM取文件数据
      this.file = obj;
      var rABS = false; //是否将文件读取为二进制字符串
      var f = this.file;
      var reader = new FileReader();
      FileReader.prototype.readAsBinaryString = function(f) {
        var binary = "";
        var rABS = false; //是否将文件读取为二进制字符串
        var pt = this;
        var wb; //读取完成的数据
        var outdata;
        var reader = new FileReader();
        reader.onload = function(e) {
          var bytes = new Uint8Array(reader.result);
          var length = bytes.byteLength;
          for (var i = 0; i < length; i++) {
            binary += String.fromCharCode(bytes[i]);
          }
          var XLSX = require("xlsx");
          if (rABS) {
            wb = XLSX.read(btoa(fixdata(binary)), {
              //手动转化
              type: "base64"
            });
          } else {
            wb = XLSX.read(binary, {
              type: "binary"
            });
          }
          outdata = XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]]); //outdata就是你想要的东西
          callback(outdata);
          _this.$message({
            type: "success",
            message: "导入文件成功！"
          });
        };
        reader.readAsArrayBuffer(f);
      };

      if (rABS) {
        reader.readAsArrayBuffer(f);
      } else {
        reader.readAsBinaryString(f);
      }
    }
  }
};
</script>


