<template>
  <div>
    <div class="main_content">
      <h3 style="text-align:center;">年级人数统计</h3>
      <hr />
      <div id="bedroom" style="float:left;"></div>
      <div id="test"></div>
    </div>
  </div>
</template>
<script>
import echarts from "echarts";
export default {
  data() {
    return {
      chart: null,
      msg: "年级人数分布图",
      gradeData: [],
      array: [],
      signDetails: [],
      dataList: [],
      className: "",
      opinion: ["初一", "初二", "初三", "高一", "高二", "高三"],
      opinionData: [
        { value: 165, name: "初一" },
        { value: 136, name: "初二" },
        { value: 170, name: "初三" },
        { value: 150, name: "高一" },
        { value: 157, name: "高二" },
        { value: 128, name: "高三" }
      ]
    };
  },
  created() {
    this.getData();
  },
  watch: {},
  computed: {},
  methods: {
    getData() {
      var token = localStorage.getItem("token");
      if (true) {
        this.$http
          .get("/getCount")
          .then(res => {
            if (res.data.code == 200) {
              var x = 0;
              for (var i = 0; i < res.data.data.length; i++) {
                x += parseInt(res.data.data[i]);
                this.gradeData.push(parseInt(res.data.data[i]));
              }
              var opinion1 = {};
              for (var i = 0; i < this.opinion.length; i++) {
                opinion1.value = parseInt(this.gradeData[i]);
                opinion1.name = this.opinion[i];
                this.dataList.push(opinion1);
                opinion1 = {};
              }
              this.chart = echarts.init(document.getElementById("bedroom"));
              this.chart.setOption({
                title: {
                  text: "年级人数分布图",
                  left: "center",
                  top: 10,
                  textStyle: {
                    fontSize: 18,
                    fontFamily: "Helvetica",
                    fontWeight: 400
                  }
                },
                tooltip: {
                  trigger: "item",
                  formatte: "{b}: {c} ({d}%)"
                },
                toolbox: {
                  feature: {
                    saveAsImage: {},
                    dataView: {}
                  },
                  right: 15,
                  top: 10
                },
                legend: {
                  orient: "vertical",
                  left: 5,
                  top: 10,
                  data: this.data
                },
                series: [
                  {
                    name: "考勤情况",
                    type: "pie",
                    radius: ["50%", "70%"],
                    center: ["50%", "60%"],
                    avoidLabelOverlap: false,
                    label: {
                      emphasis: {
                        show: true,
                        textStyle: {
                          fontSize: "24",
                          fontWeight: "bold"
                        }
                      }
                    },
                    labelLine: {
                      normal: {
                        show: false
                      }
                    },
                    data: this.dataList,//,
                    itemStyle: {
                      emphasis: {
                        shadowBlur: 10,
                        shadowOffset: 0,
                        shadowColor: "rgba(0, 0, 0, 0.5)"
                      }
                    }
                  }
                ]
              });
              console.log(this.dataList);
              this.drawLine("test", this.gradeData);
            } else {
              this.gradeData = [];
            }
          })
          .catch(err => {});
      } else {
        this.$notify({
          type: "error",
          message: "身份过期，请重新登录!"
        });
        this.$router.push("/Login");
      }
    },
    drawLine(id, gradeData) {
      this.charts = echarts.init(document.getElementById("test"));
      this.charts.setOption({
        title: {
          text: "年级人数"
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow"
          }
        },
        legend: {
          data: ["年级"]
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        xAxis: {
          type: "value",
          boundaryGap: [0, 0.01]
        },
        yAxis: {
          type: "category",
          data: ["初一", "初二", "初三", "高一", "高二", "高三", "全校人数"]
        },
        series: [
          {
            name: "年级",
            type: "bar",
            data: this.gradeData //[165, 136, 170, 150, 157, 128, 906]//gradeData
          }
        ]
      });
    },
    drawPie(id, dataList) {
      this.chart = echarts.init(document.getElementById("bedroom"));
      this.chart.setOption({
        title: {
          text: "年级人数分布图",
          left: "center",
          top: 10,
          textStyle: {
            fontSize: 18,
            fontFamily: "Helvetica",
            fontWeight: 400
          }
        },
        tooltip: {
          trigger: "item",
          formatte: "{b}: {c} ({d}%)"
        },
        toolbox: {
          feature: {
            saveAsImage: {},
            dataView: {}
          },
          right: 15,
          top: 10
        },
        legend: {
          orient: "vertical",
          left: 5,
          top: 10,
          data: this.opinion
        },
        series: [
          {
            name: "考勤情况",
            type: "pie",
            radius: ["50%", "70%"],
            center: ["50%", "60%"],
            avoidLabelOverlap: false,
            label: {
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: "24",
                  fontWeight: "bold"
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: this.opinionData, // this.dataList,//,
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffset: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)"
              }
            }
          }
        ]
      });
    }
  },
  mounted() {
    this.$nextTick(function() {
      //this.drawPie("bedroom", this.dataList);
      //this.drawPie("bedroom");
      // this.drawLine("test", this.gradeData);
    });
  }
};
</script>
<style scoped>
#bedroom {
  position: relative;
  left: 46%;
  margin-bottom: 70px;
  width: 600px;
  height: 400px;
  margin-top: 80px;
  margin-left: -555px;
  border: solid #d01257 1px;
  box-shadow: 0 0 8px #fb90b7;
  border-radius: 10px;
}

#test {
  position: absolute;
  left: 50%;
  margin-left: 130px;
  margin-bottom: 70px;
  width: 600px;
  height: 400px;
  margin-top: 80px;
  border: solid #d01257 1px;
  box-shadow: 0 0 8px #fb90b7;
  border-radius: 10px;
}

.back {
  margin-top: 550px;
}
</style>


