import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/Main'
import Courses from '@/components/Teachers/Courses'
import Home from '@/components/Home'
import Login from '@/components/Login'
import Register from '@/components/Register'
import EditInfo from '@/components/Teachers/EditInfo'
import Info from '@/components/Info'
import ChangePass from '@/components/Teachers/ChangePass'
import Note from '@/components/Note'
import Email from '@/components/Email'
import SignDetail from '@/components/SignInfo/SignDetail'
import ForgetPass from '@/components/Teachers/ForgetPass'
import Appeal from '@/components/Teachers/Appeal'
import UpdatePassword from '@/components/Teachers/UpdatePassword'
import UploadFile from '@/components/UploadFile'
import DataView from '@/components/DataView'
import UploadCourse from '@/components/Lession/UploadCourse'
import AddAccount from '@/components/Teachers/AddAccount'
import ManagerTeacher from '@/components/Teachers/ManagerTeacher'
import AddClass from '@/components/Class/AddClass'
import ClassManager from '@/components/Class/ClassManager'
import AddStudent from '@/components/Students/AddStudent'
import StudentManager from '@/components/Students/StudentManager'
import StudentDetail from '@/components/Students/StudentDetail'
import UploadStudentInfo from '@/components/Students/UploadStudentInfo'
import Analysis from '@/components/Class/Analysis'
import LessionManager from '@/components/Lession/LessionManager'
import LessionDetail from '@/components/Lession/LessionDetail'
Vue.use(Router)

export default new Router({
    routes: [{
        path: '/Login',
        name: 'Login',
        component: Login
    }, {
        path: '/',
        redirect: '/Login'
    }, {
        path: '/Register',
        name: 'Register',
        component: Register
    }, {
        path: '/Forget',
        name: 'ForgetPass',
        component: ForgetPass
    }, {
        path: '/Appeal',
        name: 'Appeal',
        component: Appeal
    }, {
        path: '/UpdatePassword',
        name: 'UpdatePassword',
        component: UpdatePassword
    }, {
        path: '/Home',
        name: 'home',
        component: Home,
        redirect: '/Courses',
        children: [{
                path: '/Courses',
                name: 'Courses',
                component: Courses
            },
            {
                path: '/Info',
                name: 'Info',
                component: Info
            },
            {
                path: '/ChangePass',
                name: 'ChangePass',
                component: ChangePass
            }, {
                path: '/EditInfo',
                name: 'EditInfo',
                component: EditInfo
            }, {
                path: '/Note',
                name: 'Note',
                component: Note
            }, {
                path: '/Email',
                name: 'Email',
                component: Email
            }, {
                path: '/SignDetail',
                name: 'SignDetail',
                component: SignDetail
            }, {
                path: '/UploadFile',
                name: 'UploadFile',
                component: UploadFile
            }, {
                path: '/DataView',
                name: 'DataView',
                component: DataView
            },
            {
                path: '/UploadCourse',
                name: 'UploadCourse',
                component: UploadCourse
            },
            {
                path: '/AddAccount',
                name: 'AddAccount',
                component: AddAccount
            }, {
                path: '/ManagerTeacher',
                name: 'ManagerTeacher',
                component: ManagerTeacher
            }, {
                path: "/AddClass",
                name: 'AddClass',
                component: AddClass
            }, {
                path: "/ClassManager",
                name: "ClassManager",
                component: ClassManager
            }, {
                path: "/AddStudent",
                name: "AddStudent",
                component: AddStudent
            }, {
                path: "/StudentManager",
                name: "StudentManager",
                component: StudentManager
            }, {
                path: "/StudentDetail",
                name: "StudentDetail",
                component: StudentDetail
            }, {
                path: "/uploadStudentInfo",
                name: "UploadStudentInfo",
                component: UploadStudentInfo
            }, {
                path: "/Analysis",
                name: "Analysis",
                component: Analysis
            }, {
                path: "/LessionManager",
                name: "LessionManager",
                component: LessionManager
            }, {
                path: "/LessionDetail",
                name: "LessionDetail",
                component: LessionDetail
            }
        ]
    }]
})


