<template>
  <div class="details" style="background:white;">
    <div class="operation1">
      <el-form
        :inline="true"
        :model="Classes"
        :rules="qurryFormRules"
        ref="Classes"
        class="demo-form-inline"
      >
        <el-form-item label="班级:" required>
          <el-select v-model="Classes.grade" placeholder="请选择班级:" style="width:200px;">
            <el-option label="初一(1)班" value="初一(1)班"></el-option>
            <el-option label="初一(2)班" value="初一(2)班"></el-option>
            <el-option label="初一(3)班" value="初一(3)班"></el-option>
            <el-option label="初一(4)班" value="初一(4)班"></el-option>
            <el-option label="初一(5)班" value="初一(5)班"></el-option>
            <el-option label="初二(1)班" value="初二(1)班"></el-option>
            <el-option label="初二(2)班" value="初二(2)班"></el-option>
            <el-option label="初二(3)班" value="初二(3)班"></el-option>
            <el-option label="初二(4)班" value="初二(4)班"></el-option>
            <el-option label="初二(5)班" value="初二(5)班"></el-option>
            <el-option label="初三(1)班" value="初三(1)班"></el-option>
            <el-option label="初三(2)班" value="初三(2)班"></el-option>
            <el-option label="初三(3)班" value="初三(3)班"></el-option>
            <el-option label="初三(4)班" value="初三(4)班"></el-option>
            <el-option label="初三(5)班" value="初三(5)班"></el-option>
            <el-option label="高一(1)班" value="高一(1)班"></el-option>
            <el-option label="高一(2)班" value="高一(2)班"></el-option>
            <el-option label="高一(3)班" value="高一(3)班"></el-option>
            <el-option label="高一(4)班" value="高一(4)班"></el-option>
            <el-option label="高一(5)班" value="高一(5)班"></el-option>
            <el-option label="高二(1)班" value="高二(1)班"></el-option>
            <el-option label="高二(2)班" value="高二(2)班"></el-option>
            <el-option label="高二(3)班" value="高二(3)班"></el-option>
            <el-option label="高二(4)班" value="高二(4)班"></el-option>
            <el-option label="高二(5)班" value="高二(5)班"></el-option>
            <el-option label="高三(1)班" value="高三(1)班"></el-option>
            <el-option label="高三(2)班" value="高三(2)班"></el-option>
            <el-option label="高三(3)班" value="高三(3)班"></el-option>
            <el-option label="高三(4)班" value="高三(4)班"></el-option>
            <el-option label="高三(5)班" value="高三(5)班"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="班级号:" required style="padding-left:200px;">
          <el-input v-model="Classes.classNo" class="classNo" placeholder="请输入班级号"></el-input>
        </el-form-item>
        <el-form-item style="padding-left:320px;">
          <el-button type="danger" @click="reset">重置</el-button>
          <el-button type="primary" @click="onSubmit">查询</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="table clearfix" style="margin-top:-15px;">
      <el-table
        :data="ClassesInfo"
        :highlight-current-row="true"
        class="tb-edit"
        height="524"
        stripe
        style="width: 100%;"
      >
        <el-table-column prop="classNo" label="班级号" width="250">
          <template slot-scope="scope">
            <el-input
              size="small"
              readonly
              v-model="scope.row.classNo"
              placeholder="请输入内容"
            ></el-input>
            <span>{{scope.row.classNo}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="grade" label="班级名" width="250">
          <template slot-scope="scope">
            <el-input
              size="small"
              readonly
              v-model="scope.row.grade"
              placeholder="请输入内容"
            ></el-input>
            <span>{{scope.row.grade}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="count" label="班级人数" width="250">
          <template slot-scope="scope">
            <el-input readonly size="small" v-model="scope.row.count" placeholder="请输入内容"></el-input>
            <span>{{scope.row.count}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="teacher" label="班主任" width="250">
          <template slot-scope="scope">
            <el-input
              size="small"
              v-model="scope.row.teacher"
              placeholder="请输入内容"
            ></el-input>
            <span>{{scope.row.teacher}}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" fixed="right">
          <template slot-scope="scope">
            <el-button @click="updateClick(scope.row)" type="primary" size="mini">更新</el-button>
            <el-button @click="deleteClick(scope.row)" type="danger" size="mini">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination" style="margin-left:40%;">
      <el-pagination
        background
        layout="prev, pager, next"
        :current-page.sync="pagination.currentPage"
        :page-size.sync="pagination.currentSize"
        :total="pagination.count"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
export default {
  name: "ClassManager",
  data() {
    return {
      Classes: {
        classNo: "",
        grade: ""
      },
      readonly:true,
      //初始分页条件
      pagination: {
        currentPage: 1, //当前页
        currentSize: 9, //每页显示的记录数
        count: 0 //记录总数
      },
      Classess: [],
      excelgrade: "",
      ClassesInfo: [],
      qurryFormRules: {}
    };
  },
  mounted() {},
  created() {
    this.getAllClasses();
    this.findByPage(this.pagination.currentPage, this.pagination.currentSize);
  },
  methods: {
    getAllClasses() {
      var token = localStorage.getItem("token");
      if (true) {
        this.$http
          .get("/findAllClass", { headers: { token: token } })
          .then(res => {
            if (res.data.code == 200) {
              //console.log("data = " + res.data.data);
              this.ClassesInfo = res.data.data;
              return this.ClassesInfo;
            }
          })
          .catch(err => {
            this.$message({
              type: "error",
              message: "网络异常"
            });
          });
      } else {
        this.$notify({
          error: "error",
          message: "身份过期，请重新登录!"
        });
        this.$router.push("/Login");
      }
    },
    updateClick(row) {
      var token = localStorage.getItem("token");
      if (true) {
          this.$confirm("确定更新吗?").then(_=>{
              var classData = {};
              classData.classNo = row.classNo;
              classData.grade = row.grade;
              classData.teacher = row.teacher;
              this.$http.post("/updateClass",classData).then((res)=>{
                  if (res.data.code == 200) {
                      this.$notify({
                          type:"success",
                          message:"更新成功!"
                      });
                      this.$router.push("/ClassManager");
                      this.ClassesInfo = this.getAllClasses();
                      this.findByPage(this.pagination.currentPage, this.pagination.currentSize);
                  }else{
                    this.$notify({
                        type:"error",
                        message:"更新失败!"
                    });
                  }
              }).catch((err)=>{
                  this.$notify({
                      type:"error",
                      message:"网络异常!"
                  })
              })
          }).catch(_=>{

          })
      }else {
          this.$notify({
              type:"error",
              message:"身份过期，请重新登录!"
          });
          this.$router.push("/Login")
      }
    },
    deleteClick(row) {
      var token = localStorage.getItem("token");
      if (true) {
        this.$confirm("确定删除吗?")
          .then(_ => {
            this.$http
              .get("/deleteClass?classNo=" + row.classNo)
              .then(res => {
                if (res.data.code == 200) {
                  this.$notify({
                    type: "success",
                    message: "操作成功"
                  });
                  this.$router.push("/ClassManager");
                  this.ClassesInfo = this.getAllClasses();
                  this.findByPage(this.pagination.currentPage, this.pagination.currentSize);
                } else {
                  this.$message({
                    type: "error",
                    message: res.data.message
                  });
                }
              })
              .catch(err => {
                this.$message({
                  type: "error",
                  message: err.data.message
                });
              });
          })
          .catch(_ => {});
      } else {
        this.$notify({
          type: "error",
          message: "身份过期，请重新登录！"
        });
        this.$router.push("/Login");
      }
    },
    //分页
    findByPage(currentPage, pageSize) {
      //const params = this.pagination
      var token = localStorage.getItem("token");
      if (true) {
        this.$http
          .get(
            "/findClassByPage?currentPage=" +
              currentPage +
              "&pageSize=" +
              pageSize
          )
          .then(res => {
            this.pagination.count = res.data.count;
            this.ClassesInfo = res.data.row;
          })
          .catch(err => {
            this.$message({
              type: "error",
              message: "网络异常"
            });
          });
      }
    },
    handleCurrentChange(page) {
      this.findByPage(page, this.pagination.currentSize);
    },
    reset() {
      this.Classes.classNo = "";
      this.Classes.grade = "";
      this.getAllClasses();
      this.findByPage(this.pagination.currentPage, this.pagination.currentSize);
    },
    onSubmit: function() {
      var token = localStorage.getItem("token");
      if (this.Classes.classNo == "" && this.Classes.grade == "") {
          return;
      }
      if (token) {
        this.$http
          .post("/findClassByCondition", this.Classes)
          .then(res => {
            if (res.data.code == 200) {
              this.$notify({
                  type:"success",
                  message:"查询成功!"
              })
              //this.$router.push("/ClassManager");
              this.ClassesInfo = res.data.data;
              //this.findByPage(this.pagination.currentPage, this.pagination.currentSize);
            } else {
              this.$notify({
                type: "error",
                message: "不存在该班级"
              });
            }
          })
          .catch(err => {
            this.$message({
              type: "error",
              message: err.data.message
            });
          });
      }
    }
  }
};
</script>
<style scoped>
.operation1 {
  padding-bottom: 5px;
  padding-top: 5px;
}
.clearfix::after {
  content: "";
  display: block;
  clear: both;
  padding-bottom: 20px;
}

.tableWrap {
  margin-top: 50px;
  border: 1px solid #333;
  height: 500px;
  width: 100%;
}

.tableWrap .item {
  width: 100px;
  border-right: 1px solid #333;
  float: left;
  height: 100%;
}
.tableWrap .item .grade {
  height: 60px;
  line-height: 60px;
  text-align: center;
  border-bottom: 1px solid #333;
}
.tableWrap .item .itemdetail {
  height: 40px;
  line-height: 40px;
  border-bottom: 1px solid #333;
}
.el-form--inline .el-form-item {
  margin-right: 0px;
}
.el-row {
  background-color: white;
}
.el-input__inner {
  padding-left: 30px;
}

.tb-edit .el-input {
  display: none;
}
.tb-edit .current-row .el-input {
  display: block;
}
.tb-edit .current-row .el-input + span {
  display: none;
}
</style>


