<template>
  <div class="details" style="background:white;">
    <div class="operation1">
      <el-form
        :inline="true"
        :model="Students"
        :rules="qurryFormRules"
        ref="Students"
        class="demo-form-inline"
      >
        <el-form-item label="学  号:" required>
          <el-input v-model="Students.studentId" class="grade" placeholder="请输入学号"></el-input>
        </el-form-item>
        <el-form-item label="姓  名:" required style="padding-left:200px;">
          <el-input v-model="Students.studentName" class="classNo" placeholder="请输入姓名"></el-input>
        </el-form-item>
        <el-form-item style="padding-left:350px;">
          <el-button type="danger" @click="reset">重置</el-button>
          <el-button type="primary" @click="onSubmit">查询</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="table clearfix" style="margin-top:-15px;">
      <el-table
        :data="StudentsInfo"
        :highlight-current-row="true"
        class="tb-edit"
        height="524"
        border
        style="width: 100%;"
      >
        <el-table-column prop="studentId" label="学  号" width="250">
          <template slot-scope="scope">
            <el-input size="small" readonly v-model="scope.row.studentId" placeholder="请输入内容"></el-input>
            <span>{{scope.row.studentId}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="studentName" label="姓  名" width="250">
          <template slot-scope="scope">
            <el-input size="small"  v-model="scope.row.studentName" placeholder="请输入内容"></el-input>
            <span>{{scope.row.studentName}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="className" label="班  级" width="250">
          <template slot-scope="scope">
            <el-input size="small" v-model="scope.row.className" readonly placeholder="请输入内容"></el-input>
            <span>{{scope.row.className}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="teacherName" label="班主任" width="250">
          <template slot-scope="scope">
            <el-input size="small" v-model="scope.row.teacherName" placeholder="请输入内容"></el-input>
            <span>{{scope.row.teacherName}}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" fixed="right">
          <template slot-scope="scope">
            <el-button @click="updateClick(scope.row)" type="primary" size="mini">更新</el-button>
            <el-button @click="deleteClick(scope.row)" type="danger" size="mini">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="opearte">
      <el-button type="primary" icon="el-icon-back" @click="back">返回上一级</el-button>
      <el-button type="primary" @click="download">
        下载班级学生信息
        <i class="el-icon-download el-icon--right"></i>
      </el-button>
    </div>
    <div class="pagination" style="margin-left:30%;">
      <el-pagination
        background
        layout="prev, pager, next"
        :current-page.sync="pagination.currentPage"
        :page-size.sync="pagination.currentSize"
        :total="pagination.count"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
export default {
  name: "StudentDetail",
  data() {
    return {
      Students: {
        studentId: "",
        studentName: ""
      },
      classNo: "",
      className: "",
      readonly: true,
      //初始分页条件
      pagination: {
        currentPage: 1, //当前页
        currentSize: 9, //每页显示的记录数
        count: 0 //记录总数
      },
      StudentsInfo: [],
      excelData: [],
      qurryFormRules: {}
    };
  },
  mounted() {},
  created() {
    this.classNo = this.$route.query.classNo;
    this.className = this.$route.className;
    localStorage.setItem("classNo",this.classNo);
    localStorage.setItem("className",this.className);
    this.getStudents();
    this.excelData = this.getStudents();
    var classno = localStorage.getItem("classNo");
    this.findByPage(this.pagination.currentPage,this.pagination.currentSize,this.classNo);
  },
  watch: {
    $route: "getStudents"
  },

  methods: {
    back() {
      this.$router.push("/StudentManager");
    },
    download() {
      require.ensure([], () => {
        const { export_json_to_excel } = require("@/excel/Export2Excel.js");
        const tHeader = ["学号", "姓名", "班级", "班主任"]; //tHeader为导出Excel表头名称，
        const filterVal = [
          "studentId",
          "studentName",
          "className",
          "teacherName"
        ];
        const list = this.excelData; // this.SignDetails为服务端返回的签到详情
        const data = this.formatJson(filterVal, list); //filterVal为传进来的数组res的键名
        var className = this.StudentsInfo[0].className;
        export_json_to_excel(tHeader, data, className+"学生信息表"); //'签到详情表'即为导出Excel名称。
      });
    },
    formatJson(filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]));
    },
    getStudents() {
      //this.StudentsInfo = this.$route.query.students;
      var classNo = localStorage.getItem("classNo");
      if (true) {
        this.$http
          .get("/getStudents?classNo=" + classNo)
          .then(res => {
            if (res.data.code == 200) {
              this.StudentsInfo = res.data.data;
              return this.StudentsInfo;
            } else {
              this.$notify({
                type: "error",
                message: "暂无数据!"
              });
            }
          })
          .catch(err => {
            this.$notify({
              type: "error",
              message: "网络异常!"
            });
          });
      } else {
        this.$notify({
          type: "error",
          message: "身份过期，请重新登录!"
        });
        this.$router.push("/Login");
      }
      //alert(this.classNo)
      return this.StudentsInfo;
    },
    updateClick(row) {
      var token = localStorage.getItem("token");
      if (true) {
        this.$confirm("确定更新吗?")
          .then(_ => {
            var student = {};
            student.studentId = row.studentId;
            student.className = row.className;
            student.teacherName = row.teacherName;
            student.studentName = row.studentName;
            this.$http
              .post("/updateStudent", student)
              .then(res => {
                if (res.data.code == 200) {
                  this.$notify({
                    type: "success",
                    message: "更新成功!"
                  });
                  this.$router.push("/StudentDetail");
                  this.StudentsInfo = this.getAllStudents();
                  var classNo = localStorage.getItem("classNo");
                  alert("classNo = " + classNo)
                  this.findByPage(
                    classNo,
                    this.pagination.currentPage,
                    this.pagination.currentSize
                  );
                } else {
                  this.$notify({
                    type: "error",
                    message: "更新失败!"
                  });
                }
              })
              .catch(err => {
                
              });
          })
          .catch(_ => {});
      } else {
        this.$notify({
          type: "error",
          message: "身份过期，请重新登录!"
        });
        this.$router.push("/Login");
      }
    },
    deleteClick(row) {
      var token = localStorage.getItem("token");
      if (true) {
        this.$confirm("确定删除吗?")
          .then(_ => {
            this.$http
              .get("/deleteStudent?studentId=" + row.studentId)
              .then(res => {
                if (res.data.code == 200) {
                  this.$notify({
                    type: "success",
                    message: "操作成功"
                  });
                  //this.$router.push({path:"/StudentDetail",classNo:this.classNo,className:this.className});
                  this.StudentsInfo = this.getStudents();
                  var classNo = localStorage.getItem("classNo");
                  this.findByPage(
                    classNo,
                    this.pagination.currentPage,
                    this.pagination.currentSize
                  );
                } else {
                  this.$message({
                    type: "error",
                    message: res.data.message
                  });
                }
              })
              .catch(err => {
                this.$message({
                  type: "error",
                  message: err.data.message
                });
              });
          })
          .catch(_ => {});
      } else {
        this.$notify({
          type: "error",
          message: "身份过期，请重新登录！"
        });
        this.$router.push("/Login");
      }
    },
    //分页
    findByPage(currentPage, pageSize, classNo) {
      //const params = this.pagination
      var token = localStorage.getItem("token");
      if (true) {
        this.$http
          .get(
            "/findStudentDetail?classNo=" +
               this.classNo +
              "&currentPage=" +
              currentPage +
              "&pageSize=" +
              pageSize
          )
          .then(res => {
            this.pagination.count = res.data.count;
            this.StudentsInfo = res.data.row;
          })
          .catch(err => {
            this.$message({
              type: "error",
              message: "网络异常"
            });
          });
      }
    },
    handleCurrentChange(page) {
      this.findByPage(page, this.pagination.currentSize,this.classNo);
    },
    reset() {
      this.Students.studentId = "";
      this.Students.studentName = "";
      this.StudentsInfo = this.getStudents();
      this.findByPage(
        this.classNo,
        this.pagination.currentPage,
        this.pagination.currentSize
      );
    },
    onSubmit: function() {
      var token = localStorage.getItem("token");
      if (this.Students.studentName == "" && this.Students.studentId == "") {
        return;
      }
      if (token) {
        this.$http
          .post("/queryStudent", this.Students)
          .then(res => {
            if (res.data.code == 200) {
              this.$notify({
                type: "success",
                message: "查询成功!"
              });
              //this.$router.push("/ClassManager");
              this.StudentsInfo = res.data.data;
              //this.findByPage(this.pagination.currentPage, this.pagination.currentSize);
            } else {
              this.$notify({
                type: "error",
                message: "不存在该学生"
              });
            }
          })
          .catch(err => {
            this.$message({
              type: "error",
              message: err.data.message
            });
          });
      }
    }
  }
};
</script>
<style scoped>
.operation1 {
  padding-bottom: 5px;
  padding-top: 5px;
}
.clearfix::after {
  content: "";
  display: block;
  clear: both;
  padding-bottom: 20px;
}

.tableWrap {
  margin-top: 50px;
  border: 1px solid #333;
  height: 500px;
  width: 100%;
}

.tableWrap .item {
  width: 100px;
  border-right: 1px solid #333;
  float: left;
  height: 100%;
}
.tableWrap .item .grade {
  height: 60px;
  line-height: 60px;
  text-align: center;
  border-bottom: 1px solid #333;
}
.tableWrap .item .itemdetail {
  height: 40px;
  line-height: 40px;
  border-bottom: 1px solid #333;
}
.el-form--inline .el-form-item {
  margin-right: 0px;
}
.el-row {
  background-color: white;
}
.el-input__inner {
  padding-left: 30px;
}
.opearte {
  margin-top: 30px;
  margin-right: 20px;
  float: right;
}
.tb-edit .el-input {
  display: none;
}
.tb-edit .current-row .el-input {
  display: block;
}
.tb-edit .current-row .el-input + span {
  display: none;
}
</style>


