<template>
  <div class="details" style="background:white;">
    <div class="table clearfix" style="margin-top:-5px;">
      <el-table
        :data="LessionDetail"
        :highlight-current-row="true"
        class="tb-edit"
        height="380"
        border
        style="width: 100%;"
      >
        <el-table-column prop="monday" label="周一" width="250"></el-table-column>
        <el-table-column prop="tuesday" label="周二" width="250"></el-table-column>
        <el-table-column prop="wednesday" label="周三" width="250"></el-table-column>
        <el-table-column prop="thursday" label="周四" width="250"></el-table-column>
        <el-table-column prop="friday" label="周五" width="250"></el-table-column>
        
      </el-table>
    </div>
    <div class="opearte" style="float:right;margin-right:10px;">
      <el-button type="primary" icon="el-icon-back" @click="back">返回上一级</el-button>
      <el-button type="primary" @click="download">
        下载课程表
        <i class="el-icon-download el-icon--right"></i>
      </el-button>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
export default {
  name: "LessionDetail",
  data() {
    return {
      Classes: {
        classNo: "",
        grade:""
      },
      className:"",
      readonly: true,
      LessionDetail: [],
    };
  },
  created() {
    var classNo = this.$route.query.classNo;
    this.className = this.$route.query.className;
    this.getLessionDetail(classNo);
    //this.findByPage(this.pagination.currentPage, this.pagination.currentSize);
  },
  methods: {
    back() {
      this.$router.go(-1);
    },
    download() {
      require.ensure([], () => {
        const { export_json_to_excel } = require("@/excel/Export2Excel.js");
        const tHeader = ["周一", "周二", "周三", "周四","z周五"]; //tHeader为导出Excel表头名称，
        const filterVal = [
          "monday",
          "tuesday",
          "wednesday",
          "thursday",
          "friday"
        ];
        const list = this.LessionDetail; // 课程详情
        const data = this.formatJson(filterVal, list); //filterVal为传进来的数组res的键名
        export_json_to_excel(tHeader, data, this.className+"课程表"); //'课程详情表'即为导出Excel名称。
      });
    },
    formatJson(filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]));
    },
    getLessionDetail(classNo) {
      var token = localStorage.getItem("token");
      //alert(classNo)
      if (true) {
        this.$http
          .get("/lession_detail?classNo="+classNo)
          .then(res => {
            //alert("resdata" +  res.data.code)
            if (res.data.code == 200) {
              console.log("data = " + res.data.data);
              this.LessionDetail = res.data.data;
              return this.LessionDetail;
            }
          })
          .catch(err => {
            this.$message({
              type: "error",
              message: "网络异常"
            });
          });
      } else {
        this.$notify({
          error: "error",
          message: "身份过期，请重新登录!"
        });
        this.$router.push("/Login");
      }
    },
    
  }
};
</script>
<style scoped>
.operation1 {
  padding-bottom: 5px;
  padding-top: 5px;
}
.clearfix::after {
  content: "";
  display: block;
  clear: both;
  padding-bottom: 20px;
}

.tableWrap {
  margin-top: 50px;
  border: 1px solid #333;
  height: 400px;
  width: 100%;
}

.tableWrap .item {
  width: 100px;
  border-right: 1px solid #333;
  float: left;
  height: 100%;
}
.tableWrap .item .grade {
  height: 60px;
  line-height: 60px;
  text-align: center;
  border-bottom: 1px solid #333;
}
.tableWrap .item .itemdetail {
  height: 40px;
  line-height: 40px;
  border-bottom: 1px solid #333;
}
.el-form--inline .el-form-item {
  margin-right: 0px;
}
.el-row {
  background-color: white;
}
.el-input__inner {
  padding-left: 30px;
}

.tb-edit .el-input {
  display: none;
}
.tb-edit .current-row .el-input {
  display: block;
}
.tb-edit .current-row .el-input + span {
  display: none;
}
</style>


