<template>
  <div>
    <div class="main_content">
      <h3 style="text-align:center;">{{msg}}</h3>
      <hr />
      <div id="bedroom" style="float:left;"></div>
      <div id="test"></div>
    </div>
    <div class="back">
      <el-button type="primary" icon="el-icon-back" @click="back">返回上一级</el-button>
    </div>
  </div>
</template>
<script>
import echarts from "echarts";
export default {
  data() {
    return {
      chart: null,
      msg: "考勤情况",
      signDetails: [],
      className: "",
      classNo: "",
      courseName:"",
      checkTime:"",
      opinion: ["旷课", "迟到", "正常"],
      opinionData: [
        { value: 25, name: "旷课" },
        { value: 3, name: "迟到" },
        { value: 7, name: "正常" }
      ]
    };
  },
  created() {
    var className = this.getData();
    this.getClassno(className);
  },
  watch: {
    $route: "getData"
  },
  methods: {
    back() {
      this.$router.go(-1);
    },
    getData() {
      this.className = this.$route.query.className;
      this.msg = this.className + this.msg;
      this.courseName = this.$route.query.courseName;
      var time = this.$route.query.checkTime;
      this.checkTime = time.split(" ")[0];
      //alert(this.checkTime)
      return this.className;
    },
    drawPie(){
      var token = localStorage.getItem("token");
      if (token) {
        this.$http.get("/")
      }
    },
    getClassno(className) {
      var token = localStorage.getItem("token");
      if (token) {
        if (className == "" || className == null) {
          return;
        }
        this.$http
          .get("/getClassno?grade="+className)
          .then(res => {
            if (res.data.code == 200) {
              this.classNo = res.data.data;
              //alert(this.classNo);
            }
          })
          .catch(err => {});
      }else {
        this.$message({
          type:"error",
          message:"身份过期，请重新登录!"
        })
      }
    },
    drawLine(id) {
      this.charts = echarts.init(document.getElementById("test"));
      this.charts.setOption({
        title: {
          text: "签到时间走势",
          left: "center",
          top: 10,
          textStyle: {
            fontSize: 18,
            fontFamily: "Helvetica",
            fontWeight: 400
          }
        },
        tooltip: {
          trigger: "item",
          formatte: "{b}: {c} ({d}%)"
        },
        toolbox: {
          feature: {
            saveAsImage: {},
            dataView: {}
          },
          right: 15,
          top: 10
        },
        xAxis: {
          type: "category",
          data: ["07:00", "07:15", "07:30", "07:45", "08:00", "08:15", "08:30"]
        },
        yAxis: {
          type: "value"
        },
        series: [
          {
            data: [0, 0, 5, 0, 0, 2, 0],
            type: "line"
          }
        ]
      });
    },
    drawPie(id) {
      this.chart = echarts.init(document.getElementById("bedroom"));
      this.chart.setOption({
        title: {
          text: "考勤情况",
          left: "center",
          top: 10,
          textStyle: {
            fontSize: 18,
            fontFamily: "Helvetica",
            fontWeight: 400
          }
        },
        tooltip: {
          trigger: "item",
          formatte: "{b}: {c} ({d}%)"
        },
        toolbox: {
          feature: {
            saveAsImage: {},
            dataView: {}
          },
          right: 15,
          top: 10
        },
        legend: {
          orient: "vertical",
          left: 5,
          top: 10,
          data: this.opinion
        },
        series: [
          {
            name: "考勤情况",
            type: "pie",
            radius: ["50%", "70%"],
            center: ["50%", "60%"],
            avoidLabelOverlap: false,
            label: {
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: "24",
                  fontWeight: "bold"
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: this.opinionData,
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffset: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)"
              }
            }
          }
        ]
      });
    }
  },
  mounted() {
    this.$nextTick(function() {
      this.drawPie("bedroom");
      this.drawLine("test");
    });
  }
};
</script>
<style scoped>
#bedroom {
  position: relative;
  left: 46%;
  margin-bottom: 70px;
  width: 600px;
  height: 400px;
  margin-top: 80px;
  margin-left: -555px;
  border: solid #d01257 1px;
  box-shadow: 0 0 8px #fb90b7;
  border-radius: 10px;
}

#test {
  position: absolute;
  left: 50%;
  margin-left: 130px;
  margin-bottom: 70px;
  width: 600px;
  height: 400px;
  margin-top: 80px;
  border: solid #d01257 1px;
  box-shadow: 0 0 8px #fb90b7;
  border-radius: 10px;
}

.back {
  margin-top: 550px;
}
</style>


